module.exports = "<app v-if=showApp></app><splash v-if=\"!showApp &amp;&amp; !showTutorial\" transition=opacity></splash><div v-if=showTutorial transition=opacity class=c-tutorial><p><span style=\"font-size: 24px\">E:\\music\\</span> <i>(емьюзик, емуз; <a href=https://vk.com/e_music target=_blank>vk.com/e_music)</a></i> — трек-блог музыки различных жанров в социальной сети Вконтакте, основанный 9 февраля 2011 года двумя микроблоггерами из Луганска. Вся сеть блогов состоит более чем из 200 пабликов, представляющих различные музыкальные направления и объединенных общим названием вида E:\\music\\%genrename%. Главный блог, как и вся сеть, преимущественно ориентированы на музыку всевозможных жанров и стилей. Причем выкладывается как «энтрилевельная музыка», так и различный андерграунд. [<a href=http://wikireality.ru/wiki/E:%5Cmusic%5C target=_blank>источник</a>]</p><p style=\"font-size: 18px\">Приложение позволяет случайным образом (с некоторой приоритетом по популярности) знакомиться с выбранным жанром, с возможостью перехода на оригинальный пост.</p><h2 style=\"margin: 40px 0 30px\">Скриншоты</h2><div class=c-tutorial__screenshots><img src=http://i.imgur.com/FVZFTl8.png /><img src=http://i.imgur.com/w5pAk1C.png /><img src=http://i.imgur.com/M4RNVZ5.png /></div><h2 @click=\"showTutorial = false\" class=c-tutorial__back><div class=\"o-btn o-btn-back\"></div><span>Назад</span></h2></div>";


