	module.exports = "<div id=app v-if=ready transition=opacity><header v-if=!showPlaylist transition=translate-from-right class=c-header><label for=header-search v-show=showEmusicLabel>E:\\music\\</label><input id=header-search v-show=!$.group placeholder=search.. v-model=$.groupsSearch onfocus=\"this.select(0, 1000)\" class=\"c-header__search u-user-select\"/><div v-if=$.group class=c-header__group>{{$.group.name}}</div><hr/></header><group-list v-if=showGroupList transition=translate-from-right :search=search></group-list><player v-if=showPlayer transition=translate-from-down></player><playlist v-if=showPlaylist transition=translate-from-down></playlist><div v-show=showBtnBack @click=\"$.group = player.group\" transition=opacity class=\"o-btn o-btn-back\"></div><div v-if=$.show.modalInteract @touchend=\"$.show.modalInteract = false\" transition=opacity class=\"c-modal c-modal--interact\"><p>You're playing music from mobile browser, which doesnt allow play audio queue! </p><p>App needs your interaction to play next song.</p><p>Just touch :)</p></div></div>";

